import HeaderIndex from "./HeaderIndex";
import Footer from "./Footer";
import React, {FC, useState} from "react";
import StartForm from "./StartForm";
import logo_ws from "../assets/logo-ws.png";
import {Path} from "../constants/path";
import HeaderCheck from "./HeaderCheck";
import Toolbar from "./Toolbar";
import data from "../assets/data.json";
import CheckButton from "./CheckButton";
import change_name_img from "../assets/Change-name.png";
import CheckListSection from "./CheckListSection";
import CheckSubbutton from "./CheckSubbutton";

const CheckPage: FC = () => {
    const [curDiv, setCurDiv] = useState(0);
    const [curSubDiv, setCurSubDiv] = useState(0);
    const [result, setResult] = useState({});
    const [checkLists, setCheckLists] = useState(JSON.parse(JSON.stringify(data)));

    const names = Object.keys(checkLists);
    const subnames = names.map(name => Object.keys(checkLists[name]));
    const buttons = names.map((name, idx) => {
        return <CheckButton
            key={idx}
            name={name}
            isActive={curDiv === idx}
            onClick={() => {setCurDiv(idx); setCurSubDiv(0)}}
        />
    });

    const handleSubmit = (code, name, per, mist, comm) => {
        let curResult = result;
        curResult[code] = [name, per, mist, comm];

        setResult({ result: curResult });
    };

    const save = () => {
        let keys = Object.keys(result);
        let table = [];
        for (let key of keys) {
            table.push([key, ...result[key]]);
        }

        let csvContent =
            "data:text/csv;charset=utf-8,%EF%BB%BF" +
            encodeURI(table.map((e) => e.join(",")).join("\n"));
        window.open(csvContent);
    };

    return (
        <div>
            <HeaderCheck />
            <main className="check-main">
                <Toolbar save={save.bind(this)} />
                <div className="check-main__content">
                    <div className="check-main__check-lists">{buttons}</div>
                    <div className="check-main__container">
                        <div className="check-main__named">
                            <input
                                type="text"
                                className="check-main__name-check"
                                placeholder="Название чек-листа"
                            />
                            <img
                                src={change_name_img}
                                alt="Изменить название"
                                id="check-name"
                            />
                        </div>
                        <div className="check-main__check">
                            <CheckListSection
                                code={checkLists[names[curDiv]][subnames[curDiv][curSubDiv]].number}
                                name={checkLists[names[curDiv]][subnames[curDiv][curSubDiv]].name}
                                wcag={checkLists[names[curDiv]][subnames[curDiv][curSubDiv]].wcag}
                                parentCallback={handleSubmit.bind(this)}
                            />
                        </div>
                        <div className="check-main__crits">
                            {
                                Object.keys(checkLists[names[curDiv]]).map((val, idx) => {
                                return (
                                    <CheckSubbutton
                                        name={checkLists[names[curDiv]][val].number}
                                        isActive={idx === curSubDiv}
                                        key={idx}
                                        onClick={() => setCurSubDiv(idx)}
                                    />
                                );
                            })}
                        </div>
                    </div>
                </div>
            </main>
            <Footer />
        </div>
    );
}

export default CheckPage;
