import React, {FC} from "react";
import { Fragment } from "react";

interface CheckButtonProps {
  name: string;
  isActive: boolean;
  onClick: () => void;
}

const CheckButton: FC<CheckButtonProps> = ({name, isActive, onClick}) => {
  const getClassNames = () => {
    const names = ["check-main__check-list"];
    if (isActive) names.push("check-list-active")
    return names.join(" ");
  }

  return (
      <button onClick={onClick} className={getClassNames()}></button>
  )
};

export default CheckButton;
